<template>
  <div>
    <p-form v-if="isForm" @result="onResult" />
    <p-result v-else :result="mamdaniResult" @repeat="toggleMode" />
  </div>
</template>

<script>
import PResult from "./presult";
import PForm from "./pform";
import { mamdaniResult } from "@/models/defaults";

export default {
  name: 'Prediksi',
  components: {
    PResult,
    PForm
  },
  data: () => ({
    mamdaniResult,
    isForm: true
  }),
  methods: {
    toggleMode() {
      this.isForm = !this.isForm;
    },
    onResult (result) {
      this.mamdaniResult = { ...result };
      this.isForm = false;
    }
  }
}
</script>

