<template>
  <v-container fluid>
    <v-container fluid class="cyan grey lighten-3">
      <v-layout row>
        <v-flex xs12 md12 class="text-xs-center">
          <div class="headline">Prediksi</div>
          <div class="body-1">Input data yang akan diprediksi</div>
          <v-btn @click="mamdani" large dark depressed color="indigo darken-2">
            Jalankan
            <v-icon right>create</v-icon>
          </v-btn>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container fluid>
      <v-layout row>
        <v-flex xs12 md6 offset-md3 class="text-xs-center">
          <v-card>
            <v-card-text>
              <v-text-field type="date" v-model="item.tanggal" label="Tanggal" />

              <v-text-field
                type="number"
                v-model.number="item.kelembapanUdara"
                label="Kelembapan Udara"
              />

              <v-text-field
                type="number"
                v-model.number="item.tekananUdara"
                label="Tekanan Udara"
                step="0.01"
              />

              <v-text-field
                type="number"
                v-model.number="item.kecepatanAngin"
                label="Kecepatan Angin"/>

              <v-text-field
                type="number"
                v-model.number="item.suhu"
                step="0.1"
                label="Suhu"/>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
import moment from "moment";
import evbus from "@/evbus";
import * as options from "@/models/options";
import constraints from "@/models/constraints";
import { Mamdani } from "@/services/mamdani";

export default {
  name: "PForm",
  data: () => ({
    item: {
      tanggal: moment().format("YYYY-MM-DD"),
      // suhu: constraints.suhu.min,
      // kecepatanAngin: constraints.kecepatanAngin.min,
      // kelembapanUdara: constraints.kelembapanUdara.min,
      // tekananUdara: constraints.tekananUdara.min,
      suhu: 28,
      kecepatanAngin: 9,
      kelembapanUdara: 67,
      tekananUdara: 1011,
      keterangan: options.keterangan[0]
    },
    options,
    constraints
  }),
  methods: {
    async mamdani() {
      const payload = { ...this.item };
      evbus.$emit("loading-on");
      let result = undefined
      try {
        const result = await Mamdani(payload);
        evbus.$emit("success", "Sukses melakukan prediksi");
        evbus.$emit("loading-off");
        this.$emit("result", result);
      } catch (err) {
        console.log(err);
        evbus.$emit("error", "Gagal melakukan prediksi");
      } finally {
        evbus.$emit("loading-off");
      }
    }
  }
}
</script>

